
import dateFormat from 'vux/src/tools/date/format.js'
import Toast from 'vux/src/components/toast/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Confirm from 'vux/src/components/confirm/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

export default {
directives: {
TransferDom
},
components: {
dateFormat,
Toast,
Confirm,
Flexbox,
FlexboxItem,
Checker,
CheckerItem,
XButton,
XInput,
Group,
XTextarea,
Icon,
LoadMore
},
data () {
return {
nofreight: localStorage.getItem('nofreight') || '',
freight: localStorage.getItem('freight') || '',
type: '',
id: '',
url: '',
data: {}
}
},
created () {
let type = this.$route.params.type / 1 
let id = this.$route.params.id 
let url = ''
this.type = type
if (type === 2) { // 积分订单
url = 'order/orderintegraldetail/'
} else {
url = 'order/orderdetail/'
}
this.getDetail(url, id)
},
methods: {
getDetail (url, id) {
this.axios.get(url + id).then((response) => {
if (response.success) {
let data = response.data
data.createDate = dateFormat(data.createDate, 'YYYY-MM-DD HH:mm:ss')
if (this.type !== 2) {
if (data.orderprolist.length > 0) {
data.orderLength = data.orderprolist.length
data.orderprolist.map((item, index) => {
item.pic = this.base_img + item.pic
switch (item.types / 1) {
case 0: item.type = ''
break
case 1: item.type = '满赠'
break
case 2: item.type = '买赠'
break
default: break
}
})
}
} else { // 积分
data.orderLength = 1
data.payamount = data.integral
data.orderprolist = [{
pid: data.proid,
pic: this.base_img + data.propic,
num: data.pronumber,
ptitle: data.proname,
amount: data.integral
}]
}
this.data = data
}
})
}
}
}



